<template>
    <div ref="bookSingleWrap" class="book-single-wrapper">
        <div class="back-wrap">
            <el-button @click="goBack">返回列表</el-button>
        </div>
        <div v-if="book" class="book-single-content" v-loading="loading">
            <div class="book-header">
                <h2 class="book-title">{{ book.Name }}</h2>
                <p class="book-desc" v-html="book.Desc"></p>
            </div>
            <div class="book-detail-wrapper">
                <div
                    v-if="book.contentInfo"
                    class="book-content-wrapper"
                    :class="`book-content-wrapper-${book.ExtendProfessionID1}`"
                >
                    <div class="right-div"></div>
                    <div
                        v-if="/^\d+$/g.test(book.contentInfo)"
                        class="book-content"
                        :class="/^\d+$/g.test(book.contentInfo) && 'img-content'"
                    >
                        <img :src="iconLink(book.contentInfo, client)" :alt="iconLink(book.contentInfo, client)" />
                    </div>
                    <template v-else>
                        <div ref="bookWrap" class="book-content" :class="isVertical ? 'vertical' : 'row'">
                            <div ref="bookTitle" class="title">{{ book.Name }}</div>
                            <div ref="bookContent" class="content" v-html="book.contentInfo"></div>
                        </div>
                        <div v-if="arrowShow" class="buttons" :class="isVertical ? 'vertical' : 'row'">
                            <div class="left" @click="toMore">
                                <i :class="isVertical ? 'el-icon-arrow-left' : 'el-icon-arrow-down'"></i>
                                <span>继续</span>
                            </div>
                            <div class="right" @click="toBack">
                                <span>返回</span>
                                <i :class="isVertical ? 'el-icon-arrow-right' : 'el-icon-arrow-up'"></i>
                            </div>
                        </div>
                        <div class="switch" @click="isVertical = !isVertical">{{ isVertical ? "古风" : "现代" }}</div>
                    </template>
                </div>
                <div class="book-info">
                    <p class="u-subtitle">
                        <svg width="23" height="18" viewBox="0 0 23 18" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" clip-rule="evenodd" d="M14.112.744l7.444 3.761a1.51 1.51 0 01.763 1.775c-.39 1.331-.347 1.89-.092 2.521.257.638.08 1.449-.587 1.87l-10.284 6.51c-.43.273-.98.315-1.457.082l-3.84-1.895-1.604-.805-1.14-.582-.696-.365-.36-.196-.26-.15C.978 12.655.66 11.405.665 10.45c.004-.971.338-2.184 1.265-2.85l.3-.207.858-.561 2.045-1.306L12.642.811a1.499 1.499 0 011.47-.067zm5.921 6.944l-8.673 5.49-.108.063-.11.052c-.11.408-.149.701-.145.973.002.162.02.328.055.517l.042.197 9.148-5.79-.072-.249a4.267 4.267 0 01-.138-.996l.001-.257zM2.8 9.678c-.09.25-.136.514-.134.78-.003.672.237 1.023.365 1.1l.308.175.626.333 1.77.903L9.01 14.6a5.01 5.01 0 01.1-1.416l.067-.288L2.8 9.678zm11.164-5.764a1 1 0 00-1.028 1.712l.1.06 2.1 1.1a1 1 0 001.028-1.712l-.1-.06-2.1-1.1z" fill="#24292E"/></svg>
                        <span>书籍信息</span>
                    </p>
                    <div class="u-book-info">
                        <div v-if="!['其它', '碑铭'].includes(getOrigin(book))" class="u-item book-origin">
                            来源：
                            <el-tooltip placement="top" popper-class="book-notice-tooltip">
                                <div slot="content">
                                    <template v-if="getOrigin(book).indexOf('秘境') > -1">
                                        <div class="u-detail-item">秘境</div>
                                        <div class="book-fb" v-html="getBossOrigin(book)"></div>
                                    </template>
                                    <template v-if="getOrigin(book).indexOf('商店') > -1">
                                        <div class="u-detail-item">商店</div>
                                        <div class="book-shop" v-html="getShopOrigin(book)"></div>
                                    </template>
                                    <template v-if="getOrigin(book).indexOf('任务') > -1">
                                        <div class="u-detail-item">任务</div>
                                        <div class="book-quest">
                                            <div
                                                class="quest-item"
                                                v-for="item in getQuestOrigin(book)"
                                                :key="item.questId"
                                            >
                                                <a target="_blank" :href="getLink('quest', item.questId)">
                                                    [{{ item.questName }}]</a
                                                >
                                            </div>
                                        </div>
                                    </template>
                                </div>
                                <span :class="getOrigin(book) !== '其它' && 'book-special'">{{ getOrigin(book) }}</span>
                            </el-tooltip>
                        </div>
                        <div v-else class="u-item">
                            来源：<span :class="getOrigin(book) !== '其它' && 'book-special'">{{
                                getOrigin(book)
                            }}</span>
                        </div>
                        <div class="u-item">
                            所属套书：{{ "【" + getProfessionType(book.ExtendProfessionID1) + "】" + book.BookName }}
                        </div>
                        <div class="u-item">阅读等级：{{ book.RequireLevel }}</div>
                    </div>
                    <template v-if="book.copy && book.copy.ID">
                        <p class="u-subtitle">
                            <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><mask id="a" style="mask-type:luminance" maskUnits="userSpaceOnUse" x="0" y="0" width="16" height="16"><path d="M1 15h3L15 4l-3-3L1 12v3z" fill="#fff" stroke="#fff" stroke-width="1.5" stroke-linejoin="round"/><path d="M9 4l3 3z" fill="#fff"/><path d="M9 4l3 3" stroke="#fff" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/></mask><g mask="url(#a)"><path d="M-.883-.38h16.97v16.971H-.882V-.379z" fill="#000"/></g></svg>
                            <span>抄录要求</span>
                        </p>
                        <div class="u-book-info">
                            <div class="u-item">
                                <span>角色等级：</span>
                                <span>{{ book.copy.RequirePlayerLevel }}</span>
                            </div>
                            <div class="u-item">
                                <span>阅读等级：</span>
                                <span>{{ book.copy.RequireLevel }}</span>
                            </div>
                            <div class="u-item">
                                <span>{{ getProfessionType(book.ExtendProfessionID1) }}等级：</span>
                                <span>{{ book.copy.RequireLevelExt }}</span>
                            </div>
                            <div class="u-item">
                                <span>精力消耗：</span>
                                <span>{{ book.copy.CostVigor }}</span>
                            </div>
                            <div v-if="book.copyList.length" class="u-item">
                                <span>所需材料：</span>
                                <item-icon
                                    v-for="meterial in book.copyList"
                                    :key="meterial.item_id"
                                    :item_id="meterial.item_id"
                                    :size="28"
                                    :amount="meterial.count"
                                    :onlyIcon="true"
                                ></item-icon>
                            </div>
                        </div>
                    </template>
                </div>
            </div>
            <!-- 套书列表 -->
            <div v-if="bookList.length" class="m-book-list" v-loading="listLoading">
                <div class="u-title">
                    <span>{{ book.BookName }}</span>
                    <a
                        v-if="book.AchievementID"
                        class="book-achievement"
                        target="_blank"
                        :href="getLink('achievement', book.AchievementID)"
                    >
                        [{{ book.achievement ? book.achievement.Name : "" }}]
                    </a>
                </div>
                <div class="book-list-wrapper">
                    <div class="book-list-content">
                        <BookCard :item="item" v-for="(item, index) in bookList" :key="item.idKey + index"></BookCard>
                    </div>
                </div>
            </div>
            <!-- 碑铭信息 -->
            <div v-if="bookMapSite.length" class="m-book-map">
                <div class="u-title">
                    <span class="u-txt">碑铭信息</span>
                </div>
                <jx3box-map
                    class="u-content"
                    :map-id="parseInt(bookMapSite[0].map)"
                    :datas="bookMapSite[0].position"
                ></jx3box-map>
            </div>
        </div>

        <!--攻略-->
        <div class="m-wiki-post-panel" v-if="wiki_post && wiki_post.post">
            <WikiPanel :wiki-post="wiki_post">
                <template slot="head-title">
                    <svg xmlns="http://www.w3.org/2000/svg" width="512" height="512" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M382.5 109.2C345.1 71.8 296.3 50 243.9 47v110.9c46.7 19.2 79.6 65.2 79.6 118.7 0 58-47.2 105.2-105.2 105.2-47.8 0-86.6-38.9-86.6-86.6 0-39.6 32.2-71.8 71.8-71.8 33.1 0 59.9 26.9 59.9 59.9h-25c0-19.3-15.7-34.9-34.9-34.9-25.8 0-46.8 21-46.8 46.8 0 34 27.6 61.6 61.6 61.6 44.2 0 80.1-35.9 80.1-80.1 0-57-46.3-103.3-103.3-103.3C122.3 173.3 63 232.6 63 305.6 63 398.4 138.5 474 231.4 474c57.1 0 110.7-22.2 151.1-62.6 40.4-40.4 62.6-94 62.6-151.1s-22.2-110.7-62.6-151.1z"/></svg>
                    <span class="u-txt">书籍攻略</span>
                </template>
                <template slot="head-actions">
                    <a class="el-button el-button--primary" :href="publish_url(`item/${id}`)">
                        <i class="el-icon-edit"></i>
                        <span>完善书籍攻略</span>
                    </a>
                </template>
                <template slot="body">
                    <div class="m-wiki-compatible" v-if="compatible">
                        <i class="el-icon-warning-outline"></i> 暂无缘起攻略，以下为重制攻略，仅作参考，<a
                            class="s-link"
                            :href="publish_url(`item/${id}`)"
                            >参与修订</a
                        >。
                    </div>
                    <Article :content="wiki_post.post.content" />
                    <div class="m-wiki-signature">
                        <i class="el-icon-edit"></i>
                        本次修订由 <b>{{ user_name }}</b> 提交于{{ updated_at }}
                    </div>
                    <Thx
                        class="m-thx"
                        :postId="id"
                        postType="item"
                        :postTitle="wiki_post.source.Name"
                        :userId="author_id"
                        :adminBoxcoinEnable="false"
                        :userBoxcoinEnable="false"
                        :authors="authors"
                        mode="wiki"
                        :key="'item-thx-' + id"
                        :client="client"
                    />
                </template>
            </WikiPanel>

            <!-- 历史版本 -->
            <WikiRevisions type="item" :source-id="id" />

            <!-- 百科评论 -->
            <WikiComments type="item" :source-id="id" />
        </div>
        <div class="m-wiki-post-empty" v-else>
            <i class="el-icon-s-opportunity"></i>
            <span>暂无攻略，我要</span>
            <a class="s-link" :href="publish_url(`item/${id}`)">完善攻略</a>
        </div>
    </div>
</template>

<script>
import Jx3boxMap from "@jx3box/jx3box-map/src/components/Map.vue";
import ItemIcon from "@/components/book/common/item_icon.vue";
import BookCard from "@/components/book/BookCard";

import bookProfession from "@/assets/data/book_profession.json";
// 碑铭坐标json
import bookMapInfoStd from "@/assets/data/stele_std_fwd.json";
import bookMapInfoOrigin from "@/assets/data/stele_origin_fwd.json";

// 副本地图json
import maps_std from "@jx3box/jx3box-data/data/fb/fb_map.json";
import maps_orgin from "@jx3box/jx3box-data/data/fb/fb_map_origin.json";

import { __imgPath } from "@jx3box/jx3box-common/data/jx3box.json";

import { postStat } from "@jx3box/jx3box-common/js/stat.js";
import { wiki } from "@jx3box/jx3box-common/js/wiki.js";

import Article from "@jx3box/jx3box-editor/src/Article.vue";
import WikiPanel from "@jx3box/jx3box-common-ui/src/wiki/WikiPanel";
import WikiRevisions from "@jx3box/jx3box-common-ui/src/wiki/WikiRevisions";
import WikiComments from "@jx3box/jx3box-common-ui/src/wiki/WikiComments";

import { publishLink, ts2str, getLink, iconLink } from "@jx3box/jx3box-common/js/utils";

import { getInfo, getList } from "@/service/book";
import { isPhone } from "@/utils/index";

export default {
    name: "bookSingle",
    components: { Jx3boxMap, BookCard, ItemIcon, WikiPanel, Article, WikiRevisions, WikiComments },
    data() {
        return {
            wiki_post: {
                source: {},
                post: null,
            },
            compatible: false,
            is_empty: true,
            // 是否古风
            isVertical: true,
            arrowShow: false,
            book: {
                idKey: "",
                Name: "",
                Desc: "",
                BookName: "",
                contentInfo: "",
            },
            loading: false,
            bookMapSite: [], // 碑铭点位信息
            listLoading: false,
            bookList: [],
        };
    },
    methods: {
        toMore() {
            const isVertical = this.isVertical;
            const bookWrap = this.$refs.bookWrap;
            if (isVertical) {
                // 竖向
                const sW = bookWrap.scrollWidth;
                const sLeft = bookWrap.scrollLeft;
                const cW = bookWrap.clientWidth;
                const step = Math.ceil(cW / 4);
                if (sW + sLeft > cW) {
                    // 没到尽头
                    bookWrap.scrollBy({
                        left: -step,
                        behavior: "smooth",
                    });
                }
            } else {
                // 横向
                const sH = bookWrap.scrollHeight;
                const sTop = bookWrap.scrollTop;
                const cH = bookWrap.clientHeight;
                const step = Math.ceil(cH / 2);
                if (sH - sTop > cH) {
                    // 没到底
                    bookWrap.scrollBy({
                        top: step,
                        behavior: "smooth",
                    });
                }
            }
        },
        toBack() {
            const isVertical = this.isVertical;
            const bookWrap = this.$refs.bookWrap;
            if (isVertical) {
                // 竖向
                const sLeft = bookWrap.scrollLeft;
                const cW = bookWrap.clientWidth;
                const step = Math.ceil(cW / 4);
                if (sLeft < 0) {
                    // 没到尽头
                    bookWrap.scrollBy({
                        left: step,
                        behavior: "smooth",
                    });
                }
            } else {
                // 横向
                const sTop = bookWrap.scrollTop;
                const cH = bookWrap.clientHeight;
                const step = Math.ceil(cH / 2);
                if (sTop > 0) {
                    // 没到顶
                    bookWrap.scrollBy({
                        top: -step,
                        behavior: "smooth",
                    });
                }
            }
        },
        goBack() {
            this.$router.push({ path: "/" });
        },
        iconLink,
        getBossOrigin(book) {
            const fbMaps = this.client === "std" ? maps_std : maps_orgin;
            const maps = Object.values(fbMaps)
                .map((item) => Object.values(item.dungeon))
                .reduce(function (a, b) {
                    return a.concat(b);
                })
                .map((item) => {
                    return item.maps.map((mItem) => {
                        return {
                            map_id: Number(mItem.map_id),
                            name: mItem.mode + item.name,
                        };
                    });
                })
                .flat();
            const drops = book?.drops;
            if (drops && drops.length) {
                let orgin = "";
                drops.forEach((item) => {
                    orgin =
                        orgin +
                        (orgin ? "<br />" : "") +
                        ("[" + item.BossName + "]") +
                        (maps.find((mItem) => mItem.map_id === item.MapID)
                            ? "(" + maps.find((mItem) => mItem.map_id === item.MapID).name + ")"
                            : "");
                });
                return orgin;
            }
            return "";
        },
        getShopOrigin(book) {
            let shopNames = book?.ShopNames;
            if (shopNames) {
                shopNames = shopNames.replace(/\|/g, "<br />");
            }
            return shopNames;
        },
        getQuestOrigin(book) {
            const quests = book?.Quests;
            let questList = [];
            if (quests) {
                questList = quests.split(";").map((item) => {
                    if (item.indexOf(":") > -1) {
                        return {
                            questId: item.split(":")[0],
                            questName: item.split(":")[1],
                        };
                    }
                });
            }
            return questList;
        },
        getOrigin(item) {
            const tempId = item.DoodadTemplateID;
            const ShopNames = item?.ShopNames;
            const drops = item.drops || [];
            const quests = item?.Quests;
            let orgin = "";
            if (tempId) {
                orgin = orgin + (orgin ? "/" : "") + (this.bookMapInfo[tempId] ? "碑铭" : "其它");
            }
            if (ShopNames) {
                orgin = orgin + (orgin ? "/" : "") + "商店";
            }
            if (drops.length) {
                orgin = orgin + (orgin ? "/" : "") + "秘境";
            }
            if (quests) {
                orgin = orgin + (orgin ? "/" : "") + "任务";
            }
            if (!orgin) {
                orgin = "其它";
            }
            return orgin;
        },
        getProfessionType(type) {
            return bookProfession.find((item) => item.id === Number(type))
                ? bookProfession.find((item) => item.id === Number(type)).name
                : "";
        },
        getData() {
            this.loading = true;
            getInfo({
                id: this.idKey,
                client: this.client,
            })
                .then((res) => {
                    const data = res.data;
                    data.contentInfo = data.contents.map((item) => item.content.replace(/\\n/g, "<br>")).join("<br>");
                    if (data.DoodadTemplateID && this.bookMapInfo[data.DoodadTemplateID]) {
                        this.bookMapSite = this.bookMapInfo[data.DoodadTemplateID];
                        this.bookMapSite[0].position[0] = Object.assign(this.bookMapSite[0].position[0], {
                            title: data.Name,
                            content: `坐标：(${this.bookMapSite[0].position[0].x},${this.bookMapSite[0].position[0].y},${this.bookMapSite[0].position[0].z})`,
                        });
                    }
                    if (data?.copy?.ID) {
                        const keyArr = Object.keys(data.copy).filter((key) => key.indexOf("RequireItem") > -1);
                        let len = parseInt(keyArr.length / 3);
                        data.copyList = [];
                        for (let i = 1; i <= len; i++) {
                            if (data.copy["RequireItemType" + i]) {
                                data.copyList.push({
                                    item_id: data.copy["RequireItemType" + i] + "_" + data.copy["RequireItemIndex" + i],
                                    count: data.copy["RequireItemCount" + i],
                                });
                            }
                        }
                    }
                    this.book = data;
                    // 保存最近阅读
                    const recentBook = {
                        idKey: data.idKey,
                        Name: data.Name,
                        Desc: data.Desc,
                        ExtendProfessionID1: data.ExtendProfessionID1,
                    };
                    this.$store.dispatch("setRecentReadList", recentBook);
                    // 获取套书列表
                    this.getBookList(data.BookName);
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        getBookList(keyword) {
            const params = {
                page: 1,
                pageSize: isPhone() ? 8 : 16,
                client: this.client,
                keyword,
            };
            this.$nextTick(() => {
                // const listWidth = this.$refs.bookSingleWrap?.clientWidth;
                // if (!isPhone() && Math.floor(listWidth / 210) < params.pageSize) {
                //     params.pageSize = Math.floor(listWidth / 210);
                // }
                this.listLoading = true;
                getList(params)
                    .then((res) => {
                        this.bookList = res.data.list || [];
                        this.listLoading = false;
                    })
                    .finally(() => {
                        this.listLoading = false;
                    });
            });
        },
        getLink,
        //百科相关
        loadData: function () {
            // 获取最新攻略
            if (this.id) {
                wiki.mix({ type: "item", id: this.id, client: this.client }, { supply: 1 }).then((res) => {
                    const { post, source, compatible, isEmpty, users } = res;
                    this.wiki_post = {
                        post: post,
                        source: source,
                        users,
                    };
                    this.is_empty = isEmpty;
                    this.compatible = compatible;
                });
            }
            this.triggerStat();
        },
        loadRevision: function () {
            // 获取指定攻略
            wiki.getById(this.post_id, { type: "item" }).then((res) => {
                this.wiki_post = res?.data?.data;
            });
            this.triggerStat();
        },
        publish_url: publishLink,
        triggerStat: function () {
            if (this.client == "origin") {
                postStat("origin_item", this.id);
            } else {
                postStat("item", this.id);
            }
        },
    },
    mounted() {
        this.getData();
    },
    computed: {
        idKey: function () {
            return this.$route.params.id;
        },
        id: function () {
            return this.book?.ItemID;
        },
        client() {
            return this.$store.state.client;
        },
        bookMapInfo() {
            return this.client === "std" ? bookMapInfoStd : bookMapInfoOrigin;
        },

        //wiki相关
        post_id: function () {
            return this.$route.params.post_id;
        },
        isRevision: function () {
            return !!this.$route.params.post_id;
        },
        author_id: function () {
            return ~~this.wiki_post?.post?.user_id;
        },
        user_name: function () {
            return this.wiki_post?.post?.user_nickname;
        },
        updated_at: function () {
            return ts2str(this.wiki_post?.post?.updated);
        },
        fav_title: function () {
            return this.wiki_post?.source?.Name;
        },
        authors: function () {
            if (!this.isRevision) {
                return (
                    this.wiki_post?.users
                        ?.filter((user) => user.id)
                        ?.map((user) => {
                            return {
                                user_id: user.id,
                                user_avatar: user.avatar,
                                display_name: user.nickname,
                            };
                        }) || []
                );
            }
            return [];
        },
    },
    watch: {
        idKey() {
            this.getData();
        },
        id() {
            if (this.post_id) {
                this.loadRevision();
            } else {
                this.loadData();
            }
            if (!/^\d+$/g.test(this.book.contentInfo)) {
                // 非图片
                this.$nextTick(() => {
                    const wrapSW = this.$refs.bookWrap.scrollWidth;
                    const wrapCW = this.$refs.bookWrap.clientWidth;
                    if (wrapSW > wrapCW) {
                        this.arrowShow = true;
                    }
                });
            }
        },
        post_id: {
            handler() {
                this.loadRevision();
            },
        },
        isVertical(bol) {
            if (!/^\d+$/g.test(this.book.contentInfo)) {
                // 非图片
                if (bol) {
                    // 竖版
                    this.$nextTick(() => {
                        const wrapSW = this.$refs.bookWrap.scrollWidth;
                        const wrapCW = this.$refs.bookWrap.clientWidth;
                        this.arrowShow = wrapSW > wrapCW;
                    });
                } else {
                    this.$nextTick(() => {
                        const wrapSH = this.$refs.bookWrap.scrollHeight;
                        const wrapCH = this.$refs.bookWrap.clientHeight;
                        this.arrowShow = wrapSH > wrapCH;
                    });
                }
            }
        },
    },
};
</script>

